import { Icon } from "@iconify/react";
import { Col, Container, Pagination, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import ProductCard from "../../ProductCard/ProductCard";
import "./InventoryroductSection.css";
import React, { useCallback, useEffect, useState } from "react";
import * as appConstants from "../../../constants";
import Category from "../../Category/Category";
import Industry  from "../../Industry/Industry";
import Type from "../../Type/Type";
import axiosClient from "../../../axiosClient";
import ItemPagenation from "../../../Pagination/ItemPagination";
import FilterBox from "../FilterBox/FilterBox"
import "./InventoryroductSection.css"
import InventoryProduct from "../InventoryProduct/InventoryProduct";

function InventoryroductSection(props) {
  const [data, setData] = useState({});
  const [page, setPage] = useState(1);
  const [textSearch, setTextSearch] = useState("");

  useEffect(() => {
    fetchData(page);
  }, [page]);

  const handleChangePage = useCallback((page) => {
    setPage(page);
    fetchData(page);
  }, []);

  const fetchData = async (p) => {
    var industryId = 0;
    var  isGlobelSearch = false;

    if(props.industryId > 0){
      industryId = props.industryId;
      isGlobelSearch = true;
    }
    debugger;
    // if(textSearch() != "" ){
     
    //   isGlobelSearch = true;
    // }

    var payload ={
      PageNumber:p,
      pageSize: 9,
      IndustryId:industryId,
      IsGlobelSearch: isGlobelSearch,
      //TextSearch:textSearch()
    }
    try {
      const res = await axiosClient.post("GetPagedItems", payload 
      
      );
     
      setData({ items: res.data, totalPages: res.totalPages });
      
    } catch (error) {
      console.log(error);
    }
  };

  const  handleInputChange = (event) => {
    const query = event.currentTarget.value;
   // setTextSearch(query)
   
  };

  return (
    <div className="HomeProductSection">
      <Container>
        <Row className="HomeBanner-row">
          <Col lg={7}>
            <Row>
              <Col lg={4}>
               
                <Industry ></Industry>
              </Col>
              <Col lg={4}>
                <Category ></Category>
              </Col>
              <Col lg={4}>
              <Type></Type>
              </Col>
            </Row>
          </Col>
          <Col lg={5}>
            <div className="searchbar mb-2">
              <input placeholder="Search equipment and solutions" onChange={handleInputChange} />
              <span><Icon icon="uil:search" /></span>
              <Link className="btn btn-blue" to="/">
                Search
              </Link>
            </div>
          </Col>
        </Row>
        <div className="product-wrap">
          <div className="product-sidebar product-sidebar-height">
            <h5>Search Result Filters</h5>
            <FilterBox />
          </div>
          <div className="product-main-list">
            <ul className="filter-tags">
              <li><span>2016 </span><Link to="/"><Icon icon="mdi:close" /></Link></li>
              <li><span>Recommended Products </span><Link to="/"><Icon icon="mdi:close" /></Link></li>
              <li><span>1 Year Old </span><Link to="/"><Icon icon="mdi:close" /></Link></li>
            </ul>
            <div>
            {data &&
                data.items &&
                data.items.length > 0 &&
                data.items.map((product) => (
                  <div className="my-3">
                     <InventoryProduct productDetail={product} />
                    </div>
                  
                ))}
             
            </div>
            {/* <Row>
              {data &&
                data.items &&
                data.items.length > 0 &&
                data.items.map((product) => (
                  <Col key={product.id} lg={4}>
                    <ProductCard RecentProductList={product} />
                  </Col>
                ))}
            </Row> */}
            {data.totalPages && data.totalPages > 1 &&(
            <ItemPagenation
            total={data.totalPages}
            current={page}
            onChangePage={handleChangePage}
            >
           </ItemPagenation>
            )}
           
            {/* <Pagination>
              <Pagination.Item>
                <Icon icon="akar-icons:chevron-left" />
              </Pagination.Item>
              <Pagination.Item>{1}</Pagination.Item>
              <Pagination.Item>{2}</Pagination.Item>
              <Pagination.Item>{3}</Pagination.Item>
              <Pagination.Ellipsis />
              <Pagination.Item>
                <Icon icon="akar-icons:chevron-right" />
              </Pagination.Item>
            </Pagination> */}
          </div>
        </div>
      </Container>
    </div>
  );
}

export default InventoryroductSection;
