import Header from '../Header/Header';
import InventoryroductSection from './InventoryroductSection/InventoryProductSection';
import './Inventory.css';
import SellWithUsBanner from '../SellWithUsBanner/SellWithUsBanner';
import Footer from '../Footer/Footer';
import React from 'react';
import {useLocation} from 'react-router-dom';

function Inventory() {
  const location = useLocation();

  var industryId  = 0;
  if(location && location.state && location.state.industryId != null && location.state.industryId > 0){
    industryId = location.state.industryId;
  }
  return (
    <div className="d-margin pt-3 inventoryPage">
      {/* <div>{location.state.name}</div> */}
        <InventoryroductSection industryId={industryId} />
        <SellWithUsBanner />
    </div>
  );
}

export default Inventory;
