import { Icon } from "@iconify/react";
import { Image } from "react-bootstrap";
import { Link } from "react-router-dom";
import "./ProductCard.css";
import React from 'react';


class ProductCard extends React.Component {

  constructor(props) {
    super(props);
    
  }

  render() {

    var coverImg = this.setCoverImage();

    return (
      <div className="ProductCardItem">
        <div className="ProductImg">
          {coverImg}
          {this.props.RecentProductList.categoryName != "" && this.props.RecentProductList.categoryName != null && (
            <Link to="/" className="whiteTag">{this.props.RecentProductList.categoryName}</Link>
          )}

          <Link to="/" className="heart-Ico">
            <Icon icon="akar-icons:heart" />
          </Link>
        </div>
        <div className="ProductInfo">

          <h5 className="title" >{this.props.RecentProductList.description}</h5>
          {/* <h5>RK5041 / 010</h5> */}
          {/* <p>
          STEP UP TRANSFORMER: INPUT 380v 30amp 3ph 3 wire - OUTPUT 8000v 3PH or
          1200v 1PH
          
        </p> */}
          <h6>
            {/* <del>$1080</del> $720.00 */}
            $ {this.props.RecentProductList.salePrice}
          </h6>
          <div className="ProductBtnWrap">
            <Link to={`/InventoryDetail/${this.props.RecentProductList.id}`} className="btn btn-blue">
              Buy Now
            </Link>
            <Link to={`/InventoryDetail/${this.props.RecentProductList.id}`} className="btn btn-blue">
              Make an Offer
            </Link>
          </div>
        </div>
      </div>
    );
  }

  setCoverImage() {
    var coverImg;
    if (this.props.RecentProductList.coverImageURL) {
      coverImg = <div className="img-fluid" style={{ height: '100%' }} dangerouslySetInnerHTML={{ __html: this.props.RecentProductList.coverImageURL }} />;
    }
    else {
      coverImg = <Image src="./assets/images/not-found.png" />;
    }
    return coverImg;
  }
}
export default ProductCard;
