import React from "react";
import { Link } from "react-router-dom";
import "./PriceCartBox.css";

function PriceCartBox() {
  return (
    <ul className="PriceboxInfo">
      <li>
        <span>Price</span>
        <h6>$22,078 USD</h6>
      </li>
      <li>
        <span>Discount</span>
        <h6>- $2,239 USD</h6>
      </li>
      <li>
        <span>Delivery charges</span>
        <h6>+ $1,132 USD</h6>
      </li>
      <li>
        <span>Freight charges</span>
        <h6>+ $1,132 USD</h6>
      </li>
      <hr />
      <li>
        <span>Sub Total</span>
        <h6>$ 20971</h6>
      </li>
      <li>
        <span>Tax</span>
        <h6>+ $4,132 USD</h6>
      </li>
      <hr />
      <li>
        <span>Total</span>
        <h4>$25,103 USD</h4>
      </li>
      <li>
        <Link to="/" className="btn btn-blue btn-checkout mt-5 d-block w-100">Checkout</Link>
      </li>
    </ul>
  );
}

export default PriceCartBox;
